/* GoogleLogin.css */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('./images/Underlay_login.png'); /* Altere para o caminho da sua imagem de fundo */
    background-size: cover;
    background-position: center;
}

.google-login-button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 500px;
    height: 80px;               /* Largura do botão */
    padding: 15px;              /* Padding */
    border: 2px solid #000;     /* Borda uniforme */
    border-radius: 50px;        /* Bordas arredondadas */
    font-size: 2rem;            /* Tamanho da fonte */
    text-align: center;         /* Centralização do texto */
    outline: none;              /* Remove a borda de foco padrão */
    margin-bottom: 20px;        /* Espaçamento inferior */
    transition: all 0.3s ease;  /* Transição suave */
    cursor: pointer;            /* Cursor de ponteiro */
    background-color: #ffb6c1;  /* Cor de fundo rosa claro */
    color: #000;                /* Cor do texto */
    font-weight: bold;          /* Texto em negrito */
}

/* Ícone do Google posicionado à esquerda */
.google-icon {
    position: absolute;
    left: 15px;             /* Ajusta o espaço entre o ícone e a borda esquerda do botão */
    width: 40px;
    height: 40px;
}

/* Efeito de hover */
.google-login-button:hover {
    border-color: #ffb6c1;      /* Cor da borda ao passar o mouse */
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2); /* Sombreamento */
}
