.quiz-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: background-image 0.3s ease-in-out, background-color 0.3s;
    text-align: center;
}

/* Overlays dinâmicos */
.overlay-initial {
    background-image: url('./images/Overlay_carta.png'); /* Caminho placeholder */
}

.overlay-quiz {
    background-image: url('./images/Overlay_quiz.png'); /* Caminho placeholder */
}

.overlay-completed {
    background-image: url('./images/Overlay_final.png'); /* Caminho placeholder */
}

/* Botão para iniciar o quiz */
.start-quiz-button {
    background-color: #ffb6c1;
    color: #000;
    border: none;
    border-radius: 8px;
    padding: 15px 30px;
    font-size: 1.5rem;
    cursor: pointer;
    transition: background-color 0.3s;
    text-align: center;
}

.start-quiz-button:hover {
    background-color: #ffa1b0;
    color: #000;
}

/* Conteúdo do quiz */
.quiz-content h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.options {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.options button {
    background-color: #ffb6c1;
    border: none;
    color: #000;
    border-radius: 8px;
    padding: 10px 20px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

.options button:hover {
    background-color: #ffa1b0;
}

.feedback {
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.9);
    color: #333;
    border: 2px solid #ccc;
    border-radius: 8px;
    padding: 20px 30px;
    font-size: 1.5rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 10;
    animation: fade-in-out 2s;
}

@keyframes fade-in-out {
    0% { opacity: 0; }
    10% { opacity: 1; }
    90% { opacity: 1; }
    100% { opacity: 0; }
}


/* Ajustes para iPad - Paisagem */
@media (min-width: 820px) and (max-width: 1080px) and (orientation: landscape) {
    .overlay-initial {
        background-image: url('./images/Overlay_carta_iPad.png');
    }

    .overlay-quiz {
        background-image: url('./images/Overlay_quiz_iPad.png');
    }

    .overlay-completed {
        background-image: url('./images/Overlay_final_iPad.png');
    }
}