/* Wrapper para manter o carrossel e o underlay */
.coupon-carousel-wrapper {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden; /* Garante que o conteúdo não saia da tela */
}

/* Imagem de fundo do underlay */
.underlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('./images/Loja_underlay.png'); /* Caminho correto para a imagem */
    background-size: cover;  /* Faz com que a imagem cubra toda a área */
    background-position: center center; /* Centraliza a imagem */
    background-repeat: no-repeat; /* Impede a repetição da imagem */
    opacity: 1; /* Opacidade ajustada */
    z-index: 1; /* Colocado atrás do conteúdo */
}

/* Estilos principais do carrossel */
.coupon-carousel {
    position: relative;
    z-index: 2; /* Faz com que o carrossel fique acima do underlay */
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 0 30px;
    box-sizing: border-box;
}

/* Contêiner dos itens do carrossel */
.carousel-item-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    transition: transform 0.5s ease; /* Transição suave para os itens */
}

/* Estilo dos itens do carrossel */
.carousel-item {
    flex: 0 0 35%;
    max-width: 360px;
    margin: 0 15px;
    text-align: center;
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.5s ease, box-shadow 0.3s ease;
}

/* Estilo do item atual no carrossel */
.carousel-item-current {
    transform: scale(1.05); /* Destaca o item atual com um leve aumento de escala */
    box-shadow: 0 8px 16px rgba(255, 182, 193, 0.6); /* Sombra rosa para destaque */
}

.carousel-item img {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
    border-radius: 10px;
    object-fit: contain;
}

/* Estilos do botão de navegação */
.carousel-button {
    background: none; /* Remove qualquer fundo */
    border: none; /* Remove qualquer borda */
    font-size: 3rem;
    cursor: pointer;
    color: #333;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 3; /* Coloca os botões acima dos itens do carrossel */
    display: flex;
    align-items: center;
    justify-content: center;
}

.carousel-button.prev {
    left: 10px;
}

.carousel-button.next {
    right: 20px;
}

/* Botão "Comprar" e botões do pop-up */
.carousel-item button,
.popup-content button {
    background-color: #ffb6c1;
    border: 2px solid #000;
    border-radius: 50px;
    padding: 15px 20px;
    font-size: 1rem;
    color: #000;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 15px;
}

/* Efeito hover para os botões */
.carousel-item button:hover,
.popup-content button:hover {
    background-color: #ffa1b0;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

/* Estilos do popup de confirmação */
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 4; /* Coloca os pop-ups acima de tudo */
}

.popup-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

/* Estilo do botão no popup */
.popup-content button {
    background-color: #ffb6c1;
    color: black;
    border-radius: 5px;
    margin: 10px;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.popup-content button:hover {
    background-color: #ffa1b0;
}

/* Ajustes para iPad - Retrato */
@media only screen and (min-width: 820px) and (max-width: 1080px) and (orientation: portrait) {

    .carousel-item {
        flex: 0 0 30%; /* Reduz a largura dos itens */
        max-width: 300px;
        padding: 15px;
    }

    .carousel-item img {
        max-width: 90%;
        max-height: 90%;
    }

    .carousel-button {
        font-size: 2.5rem; /* Botões de navegação menores */
    }

    .popup-content {
        padding: 15px;
    }

    .popup-content button {
        padding: 8px 16px;
        font-size: 0.9rem;
    }
}

/* Ajustes para iPad - Paisagem */
@media only screen and (min-width: 820px) and (max-width: 1080px) and (orientation: landscape) {

    .carousel-item {
        flex: 0 0 28%;
        max-width: 280px;
        padding: 12px;
    }

    .carousel-item img {
        max-width: 85%;
        max-height: 85%;
    }

    .carousel-button {
        font-size: 2rem;
    }

    .popup-content {
        padding: 12px;
    }

    .popup-content button {
        padding: 6px 14px;
        font-size: 0.8rem;
    }
}
